"use client";

import { TrendingUp } from "lucide-react";
import { Area, AreaChart as ReAreaChart, CartesianGrid, XAxis } from "recharts";
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "./card";
import { ChartConfig, ChartContainer, ChartTooltip, ChartTooltipContent } from "./chart";

const chartData = [
    { month: "January", desktop: 186 },
    { month: "February", desktop: 305 },
    { month: "March", desktop: 237 },
    { month: "April", desktop: 73 },
    { month: "May", desktop: 209 },
    { month: "June", desktop: 214 },
];

const chartConfig = {
    desktop: {
        label: "Desktop",
        color: "hsl(var(--chart-1))",
    },
} satisfies ChartConfig;

export default function DashboardCharts() {
    return (
        <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-6 w-full">
            {/* Bar Chart */}
            <Card className="p-4 flex flex-col items-center">
                <div className="text-lg font-semibold mb-2">Revenue Growth</div>
                <div className="w-full h-48 bg-accent/20 rounded flex items-center justify-center text-accent">
                    Bar Chart
                </div>
            </Card>
            {/* Area Chart */}
            <Card>
                <CardHeader>
                    <CardTitle>Area Chart</CardTitle>
                    <CardDescription>
                        Showing total visitors for the last 6 months
                    </CardDescription>
                </CardHeader>
                <CardContent>
                    <ChartContainer config={chartConfig}>
                        <ReAreaChart
                            accessibilityLayer
                            data={chartData}
                            margin={{ left: 12, right: 12 }}
                        >
                            <CartesianGrid vertical={false} />
                            <XAxis
                                dataKey="month"
                                tickLine={false}
                                axisLine={false}
                                tickMargin={8}
                                tickFormatter={(value) => value.slice(0, 3)}
                            />
                            <ChartTooltip
                                cursor={false}
                                content={<ChartTooltipContent indicator="line" />}
                            />
                            <Area
                                dataKey="desktop"
                                type="natural"
                                fill="var(--color-desktop)"
                                fillOpacity={0.4}
                                stroke="var(--color-desktop)"
                            />
                        </ReAreaChart>
                    </ChartContainer>
                </CardContent>
                <CardFooter>
                    <div className="flex w-full items-start gap-2 text-sm">
                        <div className="grid gap-2">
                            <div className="flex items-center gap-2 font-medium leading-none">
                                Trending up by 5.2% this month <TrendingUp className="h-4 w-4" />
                            </div>
                            <div className="flex items-center gap-2 leading-none text-muted-foreground">
                                January - June 2024
                            </div>
                        </div>
                    </div>
                </CardFooter>
            </Card>
            {/* Line Chart */}
            <Card className="p-4 flex flex-col items-center">
                <div className="text-lg font-semibold mb-2">Daily Active Users</div>
                <div className="w-full h-48 bg-accent/20 rounded flex items-center justify-center text-accent">
                    Line Chart
                </div>
            </Card>
            {/* Conversion Rate Chart */}
            <Card className="p-4 flex flex-col items-center">
                <div className="text-lg font-semibold mb-2">Conversion Rate</div>
                <div className="w-full h-48 bg-accent/20 rounded flex items-center justify-center text-accent">
                    Conversion Rate Chart
                </div>
            </Card>
            {/* Monthly Sales Chart */}
            <Card className="p-4 flex flex-col items-center">
                <div className="text-lg font-semibold mb-2">Monthly Sales</div>
                <div className="w-full h-48 bg-accent/20 rounded flex items-center justify-center text-accent">
                    Monthly Sales Chart
                </div>
            </Card>
        </div>
    );
} 